import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import M from 'materialize-css';

class Home extends Component {

  componentDidMount(){
    const slider = document.querySelector('.slider');
    M.Slider.init(slider, {
      indicators: false,
      height: 500,
      transation: 500,
      intervall: 6000,
    });

    const collapsible = document.querySelectorAll('.collapsible');
    M.Collapsible.init(collapsible, {});
  }

  render() {

    return (
      <div>
        <section className="">
          <div className="slider">
            <ul className="slides">
              <li>
                <img alt="img1" src="/static/images/img1.jpg" />
                <div className="caption center-align">
                  <h3>Lorem ipsum dolor sit amet.</h3>
                  <h5 className="light grey-text text-lighten-3 hide-on-small-only">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Eaque, voluptates.</h5>
                </div>
              </li>
              <li>
                <img alt="img2" src="/static/images/img2.jpg" />
                <div className="caption left-align">
                  <h3>Lorem ipsum dolor sit amet.</h3>
                  <h5 className="light grey-text text-lighten-3 hide-on-small-only">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Eaque, voluptates.</h5>
                </div>
              </li>
              <li>
                <img alt="img3" src="/static/images/img3.jpg" />
                <div className="caption right-align">
                  <h3>Lorem ipsum dolor sit amet.</h3>
                  <h5 className="light grey-text text-lighten-3 hide-on-small-only">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Eaque, voluptates.</h5>
                </div>
              </li>
            </ul>
          </div>
        </section>
        <section className="section indigo white-text">
          <div className="container">
            <h4>This is the Home page</h4>
            <p>
              Lorem ipsum dolor sit amet, consectetur adipisicing elit. Minus, aliquam adipisci totam consequatur porro?
              Illo iusto asperiores, assumenda esse doloribus!
            </p>
            <br />
          </div>
        </section>
        <section className="section">
          <div className="container">
            <p>This is a test server.</p>
            <p>I developed this page to test and learn routing in react</p>
            <p>Check out the links below to get some placeholder api responses!!!!</p>
            <ul className="collapsible">
              <li>
                <div className="collapsible-header"><i className="material-icons">filter_drama</i>Users</div>
                <div className="collapsible-body">
                  <p>This page will open some placeholder user data</p><br />
                  <div className="waves-effect waves-light btn">
                    <Link className="white-text" to="/users/">Users</Link>
                  </div>
                </div>
              </li>
              <li>
                <div className="collapsible-header"><i className="material-icons">place</i>Posts</div>
                <div className="collapsible-body">
                  <p>This page will open some placeholder posts data</p><br />
                  <div className="waves-effect waves-light btn">
                    <Link className="white-text" to="/posts/">Posts</Link>
                  </div>
                </div>
              </li>
              <li>
                <div className="collapsible-header"><i className="material-icons">whatshot</i>Albums</div>
                <div className="collapsible-body">
                  <p>This page will open some placeholder Albums data</p><br />
                  <div className="waves-effect waves-light btn">
                    <Link className="white-text" to="/albums/">Albums</Link>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </section>
      </div>
    );
  }
}

export default Home;
