import React, { Component } from 'react';
import $ from 'jquery';

class Albums extends Component {

  constructor(){
    super();
    this.state = {
      albums: [],
    }
  }

  getAlbums(){
    $.ajax({
      url: 'https://jsonplaceholder.typicode.com/albums/',
      dataType: 'json',
      cache: true,
      success: function(data){
        this.setState({albums: data}, function(){
          console.log("Albums recieved");
        });
      }.bind(this),
      error: function(xhr, status, err){
        console.log(err);
      }
    });
  }

  componentWillMount(){
    this.getAlbums();
  }

  componentDidMount(){
    this.getAlbums();
  }

  render() {

    let albums;
    if(this.state.albums){
      albums = this.state.albums.map(album => {
        return(
          <tr key={album.id}>
            <td>{album.id}</td>
            <td>{album.title}</td>
            <td>User{album.userId}</td>
          </tr>
        );
      });
    }

    return (
      <section className="section">
        <div className="container">
          <div className="row">
            <div className="col s12">
              <table>
                <thead>
                  <tr>
                    <th>Sl. No.</th>
                    <th>Album</th>
                    <th>Owner</th>
                  </tr>
                </thead>
                <tbody>
                  {albums}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Albums;
