import React, { Component } from 'react';
import $ from 'jquery';

class Users extends Component {

  constructor(){
    super();
    this.state = {
      users: [],
    }
  }

  getUsers(){
    $.ajax({
      url: 'https://jsonplaceholder.typicode.com/users/',
      dataType: 'json',
      cache: true,
      success: function(data){
        this.setState({users: data}, function(){
          console.log("Users recieved");
        });
      }.bind(this),
      error: function(xhr, status, err){
        console.log(err);
      }
    });
  }

  componentWillMount(){
    this.getUsers();
  }

  componentDidMount(){
    this.getUsers();
  }

  render() {

    let users;
    if(this.state.users){
      users = this.state.users.map(user => {
        return (
          <div key={user.id} className="col s12 m4">
            <div className="card teal">
              <div className="card-content white-text">
                <span className="card-title">{user.name}</span>
                <p>Username - {user.username}</p>
                <p>Email - {user.email}</p>
                <p>Phone - {user.phone}</p>
                <p>
                  Address - {user.address.suite}, {user.address.street},
                  {user.address.city}, {user.address.zipcode}
                </p>
              </div>
              <div className="card-action">
                <div className="waves-effect waves-light btn">Website</div>&nbsp;&nbsp;
                <div className="waves-effect waves-light btn">Company</div>
              </div>
            </div>
          </div>
        );
      });
    }

    return (
      <section className="section">
        <div className="Users row">
          {users}
        </div>
      </section>
    );
  }
}

export default Users;
